export const handleDownloadCV = () => {
  const pdfPath = process.env.PUBLIC_URL + '/CV_herkashyna_Mariia.pdf';
  const link = document.createElement('a');
  link.href = pdfPath;

  link.download = 'CV_herkashyna_Mariia.pdf';

  document.body.appendChild(link);
  link.click();

  document.body.removeChild(link);
};
